  -webkit-filter: grayscale(100%);
          filter: grayscale(100%);
}
.custom-indicator li.slick-active {
  -webkit-filter: grayscale(0%);
          filter: grayscale(0%);
}
.multi__image:hover {
  -webkit-transform: scale(1.1);
          transform: scale(1.1);
